"use client";

import { useState, useMemo } from "react";
import { useSearchParams } from "next/navigation";
import FilterSidebar from "./Component/FilterSidebar";
import ProductCard from "./Component/ProductCard";
import { Product } from "./Component/types/product";
import allProducts from "./Component/data/products.json";

export default function SearchPage() {
  const searchParams = useSearchParams();
  const category = searchParams.get("category") || "";

  const [filters, setFilters] = useState({
    brand: "",
    minPrice: 0,
    maxPrice: 1000
  });

  const [page, setPage] = useState(1);
  const pageSize = 12;

  const filteredProducts = useMemo(() => {
    return allProducts.filter((product: Product) => {
      return (
        (!filters.brand || product.brand.toLowerCase().includes(filters.brand.toLowerCase())) &&
        product.price >= filters.minPrice &&
        product.price <= filters.maxPrice
        // category is ignored since your current sample data has no category field
      );
    });
  }, [filters, category]);

  const paginatedProducts = filteredProducts.slice((page - 1) * pageSize, page * pageSize);
  const totalPages = Math.ceil(filteredProducts.length / pageSize);

  return (
    <div className="flex gap-6 px-6 py-8">
      <FilterSidebar filters={filters} setFilters={setFilters} />
      <main className="flex-1">
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
          {paginatedProducts.map((product) => (
            <ProductCard key={product.id} product={product} />
          ))}
        </div>
        <div className="mt-6 flex justify-center gap-2">
          {Array.from({ length: totalPages }, (_, idx) => (
            <button
              key={idx}
              className={`px-4 py-2 border rounded ${page === idx + 1 ? "bg-black text-white" : "bg-white"}`}
              onClick={() => setPage(idx + 1)}
            >
              {idx + 1}
            </button>
          ))}
        </div>
      </main>
    </div>
  );
}
